import React, { useEffect, useRef, useState, useCallback } from "react";
import styles from './index.module.scss';
import { useSpring, animated, useTransition } from '@react-spring/web';
import {
  manualClick,
  selectManualClickPower,
  selectAutoClickPower, 
  selectStage,
  selectedCountry,
  selectShowMinistryPopup,
  selectTotal,
  selectManualMultyplier,
} from "../../app/gameSlice";
import { showCoinEffectThunk } from "../../app/visualEffectsSlice";
import { useAppDispatch, useAppSelector } from "../../app/hooks";
import { Shop } from "./shop";
import { WorldMap } from "./WorldMap";
import { GOLD_COLORS } from "../../constants";
import TalentTree from "../../components/TalentTree";
import Projects from '../../components/Projects';
import ResourcesHeader from "../../components/ResourcesHeader";
import { Cosmos } from "./cosmos";
import { InstructionPopup } from "../../components/InstructionPopup";
import ShieldControls from "../../components/ShieldControls";
import { QuestSystem } from "../../components/QuestSystem/QuestSystem";
import { MinistryPopup } from "../../components/MinistryPopup/MinistryPopup";
import { UrgentMessage } from "../../components/UrgentMessage";
import classNames from "classnames";
import { useSoundIntegration } from '../../hooks/useSoundIntegration';
import { COUNTRIES } from "../../Countries";

// Компонент тултипа
const Tooltip = ({ text }: { text: string }) => {
  return (
    <div className={styles.tooltip}>
      {text}
    </div>
  );
};

interface Particle {
  x: number;
  y: number;
  vx: number;
  vy: number;
  life: number;
  color: string;
  size: number;
}



export const MainUi = () => {
  const dispatch = useAppDispatch();
  const selectedCountryName = useAppSelector(selectedCountry);
  const stage = useAppSelector(selectStage);
  const total = useAppSelector(selectTotal);
  const showMinistryPopup = useAppSelector(selectShowMinistryPopup);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const mapContainerRef = useRef<HTMLDivElement>(null);
  const manualClickPower = useAppSelector(selectManualClickPower);
  const autoClickPower = useAppSelector(selectAutoClickPower);
  const manualMultiplier = useAppSelector(selectManualMultyplier);
  const particles = useRef<Particle[]>([]);
  const [showInstructions, setShowInstructions] = useState(false);
  const [isButtonJumping, setIsButtonJumping] = useState(false);
  const [developmentWireframe, setDevelopmentWireframe] = useState(true);
  const { playClickSound } = useSoundIntegration();

  // Функция для получения центра выбранной страны
  const getSelectedCountryCenter = (): { x: number; y: number } | null => {
    if (!selectedCountryName || !mapContainerRef.current) return null;
    
    try {
      // Ищем элемент страны по атрибуту data-country
      const countryElement = mapContainerRef.current.querySelector(`[data-country="${selectedCountryName}"]`);
      if (!countryElement) return null;
      
      // Получаем bounding box элемента
      const rect = countryElement.getBoundingClientRect();
      const containerRect = mapContainerRef.current.getBoundingClientRect();
      
      // Вычисляем центр относительно контейнера карты
      const centerX = rect.left + rect.width / 2 - containerRect.left;
      const centerY = rect.top + rect.height / 2 - containerRect.top;
      
      return { x: centerX, y: centerY };
    } catch (error) {
      console.warn('Не удалось определить центр страны:', error);
      return null;
    }
  };

  // Убираем автоматический показ инструкций после выбора страны

  // Обработчик закрытия инструкций
  const handleCloseInstructions = () => {
    setShowInstructions(false);
  };

  // Инициализация частиц
  const initParticles = (x: number, y: number) => {
    // Очистить старые частицы
    particles.current = [];
    
    // Преобразование клиентских координат в координаты canvas
    if (canvasRef.current) {
      const rect = canvasRef.current.getBoundingClientRect();
      const canvasX = x - rect.left;
      const canvasY = y - rect.top;
      
      // Создаем новые частицы
      for (let i = 0; i < 30; i++) {
        const angle = Math.random() * Math.PI * 2; // Угол в радианах (360 градусов)
        const speed = 0.1 + Math.random() * 1; // Случайная скорость
        
      particles.current.push({
          x: canvasX,
          y: canvasY,
          vx: Math.cos(angle) * speed, // Вычисляем составляющую скорости по X
          vy: Math.sin(angle) * speed, // Вычисляем составляющую скорости по Y
          life: 2, // Начальное время жизни
          color: GOLD_COLORS[Math.floor(Math.random() * GOLD_COLORS.length)],
          size: 1 + Math.random() * 2 // Увеличиваем размер для лучшей видимости
        });
      }

    }
  };

  // Отрисовка частиц
  const drawParticles = useCallback(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Очищаем весь canvas
    ctx.clearRect(0, 0, canvas.width, canvas.height);

    // Если частиц нет, не продолжаем
    if (particles.current.length === 0) {
      requestAnimationFrame(drawParticles);
      return;
    }

    particles.current = particles.current.filter(p => {
      // Обновляем позицию
      p.x += p.vx;
      p.y += p.vy;
      
      // Добавляем небольшое замедление
      p.vx *= 0.98;
      p.vy *= 0.98;
      
      // Уменьшаем время жизни (медленнее)
      p.life -= 0.01;

      // Рисуем частицу, если она еще жива
      if (p.life > 0) {
        ctx.save();
        ctx.fillStyle = p.color;
        ctx.globalAlpha = p.life; // Прозрачность зависит от времени жизни
        
        // Рисуем круг
      ctx.beginPath();
        ctx.arc(p.x, p.y, p.size, 0, Math.PI * 2);
      ctx.fill();

        // Восстанавливаем контекст
        ctx.restore();
      }

      // Сохраняем частицу, пока она жива
      return p.life > 0;
    });

    requestAnimationFrame(drawParticles);
  }, []);

  // Обновляем обработчик клика по стране
  const handleCountryClick = (pos: { x: number; y: number }, countryId?: string) => {
    
    // Играем звук клика в зависимости от этапа
    playClickSound(stage === 2 ? 'planet' : 'country');
    // get manualclick bonus from country
    const manualClickBonus = countryId === selectedCountryName ? 0 : countryId ? COUNTRIES[countryId].manualPower : 0;
    // Генерируем ресурсы
    dispatch(manualClick(manualClickBonus));
    

      // Показываем эффект монет
      dispatch(showCoinEffectThunk({
        position: pos,
        value: (manualClickPower + manualClickBonus) * manualMultiplier,
        prefix: '+',
        suffix: ' ₿'
      }));

    
    // Инициализируем частицы, используя оригинальные координаты клика
    initParticles(pos.x, pos.y);
  };

  // Запуск анимации частиц
  useEffect(() => {
    const animationId = requestAnimationFrame(drawParticles);
    return () => cancelAnimationFrame(animationId);
  }, [drawParticles]);


  // Задать размеры canvas при монтировании и изменении размера окна
  useEffect(() => {
    const updateCanvasSize = () => {
      const canvas = canvasRef.current;
      const container = mapContainerRef.current;
      if (canvas && container) {
        // Получаем размеры контейнера
        const { width, height } = container.getBoundingClientRect();
        
        // Устанавливаем размеры canvas равными размерам контейнера
        canvas.width = width;
        canvas.height = height;

      }
    };

    // Обновляем размер при монтировании
    updateCanvasSize();
    
    // Обновляем размер при изменении окна
    window.addEventListener('resize', updateCanvasSize);
    
    return () => window.removeEventListener('resize', updateCanvasSize);
  }, []);

  // Эффект для показа автодобычи каждую секунду
  useEffect(() => {
    if (!selectedCountryName || autoClickPower <= 0) return;

    const interval = setInterval(() => {
      const countryCenter = getSelectedCountryCenter();
      if (countryCenter) {
        // Показываем эффект автодобычи с преобразованием в глобальные координаты
        const mapContainer = mapContainerRef.current;
        if (mapContainer) {
          const containerRect = mapContainer.getBoundingClientRect();
          // Преобразуем координаты в глобальные (относительно окна)
          const globalPos = {
            x: containerRect.left + countryCenter.x + window.scrollX,
            y: containerRect.top + countryCenter.y + window.scrollY
          };
          
          dispatch(showCoinEffectThunk({
            position: globalPos,
            value: autoClickPower,
            prefix: '+',
            suffix: '₿'
          }));
        }
      }
    }, 1000); // Каждую секунду

    return () => clearInterval(interval);
  }, [selectedCountryName, autoClickPower, dispatch]);


  return (
    <div className={classNames({
      'step1': true,
      [styles.step1]: true,
      [styles.sidebar]: true, // (total > 10),
      [styles.talent]: true, //(total > 100)
    })}>
    {showInstructions && (
      <InstructionPopup 
        onClose={handleCloseInstructions} 
        isButtonJumping={isButtonJumping}
        setIsButtonJumping={setIsButtonJumping}
      />
    )}
    <header className={styles.mainHeader}>
      <ResourcesHeader/>      
    </header>
    <div className={classNames(styles.development)}>
      <header className={styles.header}><span>Развитие</span></header>
      <TalentTree />
      {stage === 2 && (
        <div className={styles.shieldControlsContainer}>
          <ShieldControls />
        </div>
      )}
    </div>
    

      <div className={classNames(styles.mapContainer, stage === 2 && styles.cosmosContainer)} ref={mapContainerRef}>
      {(
        <>
        {stage === 2 ? (
          <Cosmos onEarthClick={handleCountryClick} />
        ) : (
          <WorldMap onCountryClick={handleCountryClick} />
        )}
          <canvas
            ref={canvasRef}
            className={styles.particlesCanvas}
            style={{ zIndex: 10 }}
          />
        </>
      )}
    </div>

    <div className={classNames(styles.projects)}>
      <header className={styles.header}><span>Проекты</span></header>
      <Projects />
    </div>
    <div className={classNames(styles.upgrades)}>
      <header className={styles.header}>
        <span>Улучшения</span>
        <div className={styles.tooltipContainer}>
          <span className={styles.infoIcon}>ⓘ</span>
          <Tooltip text="Улучшайте свои инструменты. Здесь улучшения ваших кликов, автоматические клики и автоматические клики для определенной сферы. У каждого улучшения доход указан как число, что будет добавлено к уже имеющемуся значению." />
        </div>
      </header>
      <Shop/>
    </div>

    {/* Система квестов */}
    <QuestSystem />

    {/* Попап министерства финансов */}
    {showMinistryPopup && <MinistryPopup />}

    <UrgentMessage />
    </div>
  );
};
