import { useAppSelector } from '../app/hooks';
import { selectGame } from '../app/gameSlice';
import { SHIP_DAMAGE_WAVES } from '../app/constants';

interface DamageWave {
  isActive: boolean;
  waveIndex: number;
  timeRemaining: number;
  multiplier: number;
  message: string;
}

const WAVE_MESSAGES = [
  "Мы в поле астероидов!",
  "Повышенная опасность!",
  "Неизвестные искажения пространства!",
  "Критическая зона активности!",
  "Чрезвычайная космическая аномалия!"
];

export const useDamageWave = (): DamageWave => {
  const game = useAppSelector(selectGame);

  // Если не во втором этапе или полет не начат, возвращаем неактивную волну
  if (game.stage !== 2 || game.ship.flightStartTime === 0) {
    return {
      isActive: false,
      waveIndex: -1,
      timeRemaining: 0,
      multiplier: 1,
      message: ""
    };
  }

  const now = Date.now();
  const flightTime = now - game.ship.flightStartTime;

  // Проверяем каждую волну
  for (let i = 0; i < SHIP_DAMAGE_WAVES.length; i++) {
    const wave = SHIP_DAMAGE_WAVES[i];
    const waveEndTime = wave.startTime + wave.duration;
    
    // Если время полета попадает в интервал волны
    if (flightTime >= wave.startTime && flightTime <= waveEndTime) {
      const timeRemaining = waveEndTime - flightTime;
      
      return {
        isActive: true,
        waveIndex: i,
        timeRemaining,
        multiplier: wave.multiplier,
        message: WAVE_MESSAGES[i] || "Опасная зона!"
      };
    }
  }

  // Если не попали ни в одну волну
  return {
    isActive: false,
    waveIndex: -1,
    timeRemaining: 0,
    multiplier: 1,
    message: ""
  };
}; 