import React, { useMemo, useState, useEffect } from 'react';
import styles from './index.module.scss';
import { useAppSelector } from '../../app/hooks';
import { selectResources, selectManualClickPower, selectAutoClickPower, selectedCountry, selectStage, selectDeathTimer, selectTalentPoints, selectSpentTalentPoints } from '../../app/gameSlice';
import CountUp from '../CountUp';
import classNames from 'classnames';

interface TooltipData {
  isVisible: boolean;
  type: 'sphere' | 'main' | 'country' | 'earth' | 'timer' | 'talents';
  sphere?: string;
  position: { x: number; y: number };
}

const tooltipInfo: Record<string, string> = {
  'economy': 'Экономика: получайте ресурсы, улучшая рынки, банки. Для запуска корабля надо 200000💸',
  'science': 'Наука: развивайте технологии, строя спутники и лаборатории. Для запуска корабля надо 20000🧬',
  'faith': 'Вера: расширяйте свое влияние через храмы и крестоносцев. Для запуска корабля надо 20000⛪️',
  'main': '', // будет заполнено динамически
  'country': 'Выбранная страна, которой вы сейчас управляете. Объедините Землю, для противостояния инопланетной угрозе.',
  'earth': 'Теперь вы управляете Землей, судьба человечества лежит на ваших плечах.',
  'talents': '' // будет заполнено динамически
};

const ResourcesHeader: React.FC<{ wireframe?: boolean }> = ({ wireframe = false }) => {
  const resources = useAppSelector(selectResources);
  const clickPower = useAppSelector(selectManualClickPower);
  const autoClickPower = useAppSelector(selectAutoClickPower);
  const countryName = useAppSelector(selectedCountry);
  const stage = useAppSelector(selectStage);
  const deathTimer = useAppSelector(selectDeathTimer);
  const talentPoints = useAppSelector(selectTalentPoints);
  const spentTalentPoints = useAppSelector(selectSpentTalentPoints);
  const [tooltip, setTooltip] = useState<TooltipData>({ 
    isVisible: false,
    type: 'sphere',
    position: { x: 0, y: 0 } 
  });
  const [, setUpdate] = useState(0); // Для принудительного обновления таймера

  const handleMouseEnter = (type: 'sphere' | 'main' | 'country' | 'earth' | 'timer' | 'talents', sphere?: string, e?: React.MouseEvent) => {
    if (!e) return;
    
    const rect = e.currentTarget.getBoundingClientRect();
    setTooltip({
      isVisible: true,
      type,
      sphere,
      position: { 
        x: rect.left, 
        y: rect.bottom + 5 
      }
    });
  };

  const handleMouseLeave = () => {
    setTooltip({ ...tooltip, isVisible: false });
  };

  // Динамическое содержимое тултипа
  const getTooltipContent = () => {
    switch (tooltip.type) {
      case 'sphere':
        if (tooltip.sphere) {
          return tooltipInfo[tooltip.sphere] || `${tooltip.sphere}: Развивайте эту сферу через специальные улучшения.`;
        }
        break;
      case 'main':
        return `Кликай на страну: +${Math.round(clickPower)}₿ за клик. Общий CPS: ${autoClickPower.toFixed(1)}₿/сек.`;
      case 'country':
        return tooltipInfo.country;
      case 'earth':
        return tooltipInfo.earth;
      case 'timer':
        const timeRemaining = getGameTimeRemaining();
        return timeRemaining 
          ? `До второго удара осталось ${timeRemaining.years} лет, ${timeRemaining.months} месяцев, успейте построить звездолет`
          : 'Успейте построить звездолет, прежде чем произойдет второй удар!';
      case 'talents':
        return `Очки талантов: ${talentPoints} доступно, ${spentTalentPoints} потрачено`;
      default:
        break;
    }
    return '';
  };
  // Функция для расчета оставшегося времени в игровых годах
  const getGameTimeRemaining = () => {
    if (!deathTimer.isActive || stage !== 1) return null;
    
    const elapsed = Date.now() - deathTimer.startTime;
    const remaining = deathTimer.totalDuration - elapsed;
    
    if (remaining <= 0) return { years: 0, months: 0 };
    
    // Рассчитываем оставшиеся игровые годы
    const progress = elapsed / deathTimer.totalDuration;
    const yearsElapsed = progress * deathTimer.gameYears;
    const yearsRemaining = deathTimer.gameYears - yearsElapsed;
    
    const years = Math.floor(yearsRemaining);
    const months = Math.floor((yearsRemaining - years) * 12);
    
    return { years, months };
  };

  // Функция для расчета текущей игровой даты
  const getCurrentGameDate = () => {
    if (!deathTimer.isActive || stage !== 1) return null;
    
    const elapsed = Date.now() - deathTimer.startTime;
    const progress = elapsed / deathTimer.totalDuration;
    const yearsElapsed = progress * deathTimer.gameYears;
    
    const startDate = new Date(deathTimer.realStartDate);
    const currentYear = startDate.getFullYear() + Math.floor(yearsElapsed);
    const currentMonth = startDate.getMonth() + Math.floor((yearsElapsed - Math.floor(yearsElapsed)) * 12);
    
    // Обработка переполнения месяцев
    const finalYear = currentYear + Math.floor(currentMonth / 12);
    const finalMonth = currentMonth % 12;
    
    return new Date(finalYear, finalMonth);
  };

  const getRandomEarth = useMemo(() => {
    const names = ['𒆠', 'Erṣetu', 'Terra', 'Miðgarðr', 'Tlālticpac', '🌍']
    return Math.random() <= 0.05 ? names[Math.floor(Math.random() * names.length)] : 'Earth';
  }, []);

  // Обновляем таймер каждую секунду
  useEffect(() => {
    if (stage === 1 && deathTimer.isActive) {
      const interval = setInterval(() => {
        setUpdate(prev => prev + 1);
      }, 1000);

      return () => clearInterval(interval);
    }
  }, [stage, deathTimer.isActive]);

  return (
    <div className={classNames(styles.resourcesHeader, { [styles.wireframe]: wireframe })}>
      
      <div 
        className={styles.talentPoints}
        onMouseEnter={(e) => handleMouseEnter('talents', undefined, e)}
        onMouseLeave={handleMouseLeave}
      >
        <div className={styles.icon}>⭐</div>
        <div className={styles.talentValue}>
          <span className={styles.available}>{talentPoints}</span>
        </div>
      </div>
      
      <div 
        className={styles.countryInfo}
        onMouseEnter={(e) => handleMouseEnter(
          stage === 2 ? 'earth' : (stage === 1 && deathTimer.isActive ? 'timer' : 'country'), 
          undefined, 
          e
        )}
        onMouseLeave={handleMouseLeave}
      >
        {(() => {
          if (stage === 2) {
            return getRandomEarth;
          } else if (stage === 1 && deathTimer.isActive) {
            const currentDate = getCurrentGameDate();
            if (currentDate) {
              const month = currentDate.toLocaleDateString('ru-RU', { month: 'long' });
              const year = currentDate.toLocaleDateString('ru-RU', { year: 'numeric' });
              return (
                <div className={styles.dateDisplay}>
                  <span className={styles.monthPart}>{month}</span>
                  <span className={styles.yearPart}>{year}</span>
                </div>
              );
            }
            return 'Неизвестная дата';
          } else {
            return countryName || 'Выберите страну';
          }
        })()}
      </div>
      
      <div 
        className={styles.mainResources}
        onMouseEnter={(e) => handleMouseEnter('main', undefined, e)}
        onMouseLeave={handleMouseLeave}
      >
        <div className={styles.icon}>💰</div>
        <CountUp value={resources} suffix="₿" duration={950} showColors className={styles.value} />
      </div>
      
      
      
      {tooltip.isVisible && (
        <div 
          className={styles.tooltip} 
          style={{ 
            left: `${tooltip.position.x}px`, 
            top: `${tooltip.position.y}px` 
          }}
        >
          {getTooltipContent()}
        </div>
      )}
    </div>
  );
};

export default ResourcesHeader; 